.page {
  min-height: 100vh;
  position: relative;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.hero {
  display: flex;
  align-items: center;
  gap: 3rem;
  min-height: 80vh;
  flex-wrap: wrap;
}

.heroImage {
  position: relative;
  flex-shrink: 0;
}

.imageGlow {
  position: absolute;
  inset: -10px;
  border-radius: 50%;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.3), rgba(118, 75, 162, 0.3));
  filter: blur(20px);
}

.profileImage {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 50%;
  position: relative;
  z-index: 1;
}

.heroContent {
  flex: 1;
  min-width: 280px;
}

.name {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.roleContainer {
  margin-bottom: 1rem;
}

.role {
  font-size: 1.25rem;
  opacity: 0.9;
}

.description {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.heroButtons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
